export default {
  props: {
    showFlag: {
      type: Boolean,
      default: false
    },
    isNight: {
      type: Boolean,
      default: false
    },
    currentIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      setting: false,
      color: ["#f6f6f6", "#e1d4b2", "#bed8ab", "#efc1c1", "#dcc49e", "#d9e8ff"]
    };
  },
  computed: {
    modeCls() {
      return this.isNight ? "icon-brightness" : "icon-night";
    },
    modeText() {
      return this.isNight ? "白天" : "夜间";
    }
  },
  methods: {
    back() {
      this.$emit("back");
    },
    openChapters() {
      this.$emit("openChapters");
    },
    changeMode() {
      this.$emit("changeMode");
    },
    openSetting() {
      this.setting = !this.setting;
      this.$emit("openSetting");
    },
    changeColor(item, index) {
      this.$emit("changeColor", item, index);
    },
    minusFontSize() {
      this.$emit("minusFontSize");
    },
    addFontSize() {
      this.$emit("addFontSize");
    }
  },
  watch: {
    showFlag(newS) {
      if (newS === false) {
        this.setting = false;
      }
    }
  }
};